@charset "utf-8";

// Fonts

/* merriweather-regular - latin */
@font-face {
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  src: local('Merriweather Regular'), local('Merriweather-Regular'),
       url('../assets/fonts/merriweather-v21-latin-regular.woff2') format('woff2'),
       url('../assets/fonts/merriweather-v21-latin-regular.woff') format('woff');
}

/* lato-700 - latin */
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 700;
  src: local('Lato Bold'), local('Lato-Bold'),
       url('../assets/fonts/lato-v16-latin-700.woff2') format('woff2'),
       url('../assets/fonts/lato-v16-latin-700.woff') format('woff');
}


// Define defaults for each variable.

$base-font-family: Merriweather !default;
$base-font-size:   16px !default;
$base-font-weight: 400 !default;
$small-font-size:  $base-font-size * 0.875 !default;
$base-line-height: 1.5 !default;

$heading-font-family: Lato !default;

$spacing-unit:     30px !default;

$text-color:       #292929 !default;
$background-color: #f7f7f7 !default;
$brand-color:      #2a7ae2 !default;

$grey-color:       #828282 !default;
$grey-color-light: lighten($grey-color, 40%) !default;
$grey-color-med:   darken($grey-color, 15%) !default;
$grey-color-dark:  darken($grey-color, 25%) !default;

$table-text-align: left !default;

// Width of the content area
$content-width:    750px !default;

$on-palm:          600px !default;
$on-laptop:        750px !default;

// Use media queries like this:
// @include media-query($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit / 2;
//     padding-left: $spacing-unit / 2;
//   }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

@mixin media-query-min($device) {
  @media screen and (min-width: $device) {
    @content;
  }
}

@mixin relative-font-size($ratio) {
  font-size: $base-font-size * $ratio;
}

// Import partials.
@import
  "minima/normalize",
  "minima/base",
  "minima/layout",
  "minima/ue-scroll",
  "minima/friendly-syntax",
  "minima/print"
;
